


body {
	font-family:@body-font;
}

h1, h2, h3, h4, h5, h6 {
  font-family:@h-font;
  font-weight: normal !important;
}
@import "gridlover"; // generate on gridlover.net


a {
  background:transparent;
  text-decoration: none;
  color:@link-color;
}
a:focus {
  outline:thin dotted;
  color:@link-color;
}
a:active {
  outline:0;
}
a:hover {
  outline:0;
  color:@link-color;
}


a:visited {
  color:@link-color;
}
a:hover, a:visited:hover, a:visited:focus {
  color:@link-color;
}
a:link, a:visited:link {
  -webkit-tap-highlight-color:rgba(0,0,0,0.3);
}


.cta {
  display: block;
  background: @dark;
  color: @white;
  font-family: @h-font;
  position: relative;
  padding: 5%;
  text-align: center;
  margin: 1em auto;
  z-index: 1;
  &::after {
    content: "";
    display: block;
    position: absolute;

    margin: auto;
    background: transparent;
    border-width: 0 0.3em 0.3em 0.3em;
    border-style: solid;
    border-color: @lighter-grey;
    z-index: -1;

    top: 30%;
    bottom: -0.3em;
    left: -0.3em;
    right: -0.3em;
    transition: border-color 0.7s ease, transform 0.2s ease;

  }

  

  &:hover {
    &:after {
      transform:translateY(-0.15em);
      border-color: @highlight;
    }
  }
}