
// mostly this is setting variables to change the look of the site.


// ORNC STYLES


// COLOURS

@darkblue : #3b455e;
@lightblue : #687f91;
@off-lightblue : #8296A6;
@this-website: ~'http://ornc.local'; // this will have to change 

// We use generic colour names to imply something so set them here.  
@body-font : "ff-meta-web-pro";
@h-font :'Felix Titling W00';

@dark : @darkblue;
@light : @lightblue;
@lighter : @off-lightblue;

@med-dark : #506077;

// used for navigation, amongst other things.
@highlight : #fcd27c;
@highlight-text : @black;

@emphasis : @darkblue;
@emphasis-text: @white; // if @emphasis was a background etc.

@link-color : @highlight;

// general colour as a default - this just sets the body tag
@default-text : @lightgrey;

@byline: @lighter;

// Header Styles and sizes

//@lighter-grey; @white

@nav-highlight : @white;
@nav-highlight-text : @lightblue;

@nav-secondary-highlight : @light;
@nav-secondary-highlight-text : @white;


// carousel styles
@cycle-carousel-width : 100%;
@carousel-next-fa-icon : @fa-var-chevron-right;
@carousel-prev-fa-icon : @fa-var-chevron-left;
@skip-offset : 5%;
@skip-button-color : RGBA(255,255,255,0.8);
@carousel-aspect-ratio : (4 / 6);
@slide-background: @darkgrey;

@carousel-max-height: 600px;
@carousel-height: 600px; 



//mobile and headers stuff


@mobile-line-height: 20px;
@header-height-px : 60px;


@content-margin : 20px; // better than em because of positioning absolutes

// use mixins to make cleaner CSS, but keep the config here.

.body-style() {
	color: rgba(236, 230, 218, 1);
	background: @darkblue; // may want image.
}



.footer-style() {
	background: @lightgrey;
}




.header-style() {
	background: @lighter;
  height: @header-height-px;
  padding: (@header-height-px * 0.15) 0;
  position: fixed;
  top: 0;
}


.admin-bar {
  .header {
    top: 32px !important;
  }


  #cta-sidebar {
    top: 32px !important;
  }

  #container {
    top: @header-height-px + 32px !important;
  }
}




.content-style() { // #content style
  margin-top: @content-margin + @header-height-px;

  @media screen and (max-width: 900px) {
    #main {
      width: 92%;
    }
  }
}


#container {
  top: @header-height-px;
}



.page-template-template-home #content {
  margin-top: @content-margin;
}

.nav-li-style() {
  margin: 0; // it's coming from the right so the trailing margin is fine
  
  height: 100%;
  background: rgba(0,0,0,0.2); // debug

  border-right:2px solid transparent;
  background-clip: border-box; 

  a {
    padding: 5px 10px;
  }

  .sub-menu, .children {
     right: -2px !important; // account for the border
  }
  &:hover {
    border-right:2px solid RGBA(0,0,0,0.4);

    .sub-menu, .children {
      padding: 0;
      border-right:2px solid RGBA(0,0,0,0.4);
      border-bottom: 2px solid RGBA(0,0,0,0.4);
     
      background-clip: border-box; 
    }
  }
}


@mobile-cta-height : 75px;
.popup-styles() {
  // this is for the popup mobile menu: #popup-menu-container is this basically.  

  // because CTA exists:
  top: @mobile-cta-height + @header-height-px;

  padding-top: 25px;

  nav[role="navigation"] .nav > li {
    border-right: none !important;
  }

  ul {

      border-right: none !important;
      border-bottom: none !important;

  }


}

.nav-li-children-style() {
  margin: 0; // it's coming from the right so the trailing margin is fine
  
}

body {
	.body-style();
}


.nav-font-style() {
  text-transform: uppercase;
  letter-spacing: 0.2em;
}



.full-width-background-maker(@color : RGBA(0,0,0,0.5)) {
	position: relative;
	padding-top: 1em;
	padding-bottom: 1em;

  &:before {
    content: "";
    width: 4000px;
    left: -1000px;
    background: @color;
    top: 0;
    bottom: 0;
    display: block;
    position: absolute;
    z-index: -1;
  }
}

#content {
	width: 100%;
}
#inner-content {
	
    .full-width-background {
      .full-width-background-maker(@light);

      &.blue {
      	&:before {
      		//background: @blue;
      	}
      }
    }
}